import React, { Component } from 'react';
import './App.css';
import { Layout, Header, Navigation, Drawer, Content } from 'react-mdl';
import Main from './components/main';
import { Link } from 'react-router-dom';
import ReactGA from 'react-ga';

class App extends Component {
	constructor(props) {
		super(props);
		const trackingId = "UA-83942261-2";
		ReactGA.initialize(trackingId);
		ReactGA.pageview(window.location.pathname);
	}

	render() {
		return (
			<Layout className="layout">
				<Header className="header-color" title={<Link style={{ textDecoration: 'none', color: 'white' }} to="/">Wan Song Lee</Link>} scroll>
					<Navigation>
						<Link to="/">Home</Link>
						<Link to="/projects">Projects</Link>
						<Link to="/resume">Resume</Link>
					</Navigation>
				</Header>
				<Drawer title={<Link style={{ textDecoration: 'none', color: 'black' }} to="/">Wan Song Lee</Link>}>
					<Navigation>
						<Link to="/">Home</Link>
						<Link to="/projects">Projects</Link>
						<Link to="/resume">Resume</Link>
					</Navigation>
				</Drawer>
				<Content id="content">
					<Main />
				</Content>
			</Layout>
		);
	}
}

export default App;
